, Empfänger und Zweck Ihrer gespeicherten\npersonenbezogenen Daten zu erhalten. Sie haben außerdem ein Recht, die Berichtigung oder Löschung dieser\nDaten zu verlangen. Hierzu sowie zu weiteren Fragen zum Thema Datenschutz können Sie sich jederzeit unter der\nim Impressum angegebenen Adresse an uns wenden. Des Weiteren steht Ihnen ein Beschwerderecht bei der\nzuständigen Aufsichtsbehörde zu.")]),_c('p',[_vm._v("Außerdem haben Sie das Recht, unter bestimmten Umständen die Einschränkung der Verarbeitung Ihrer\npersonenbezogenen Daten zu verlangen. Details hierzu entnehmen Sie der Datenschutzerklärung unter „Recht\nauf Einschränkung der Verarbeitung“.")]),_c('h2',[_vm._v("2. Hosting")]),_c('h3',[_vm._v("Externes Hosting")]),_c('p',[_vm._v("Diese Website wird bei einem externen Dienstleister gehostet (Hoster). Personenbezogenen Daten, die auf dieser\nWebsite erfasst werden, werden auf den Servern des Hosters gespeichert. Hierbei kann es sich v. a. um IP-Adressen,\nKontaktanfragen, Meta- und Kommunikationsdaten, Vertragsdaten, Kontaktdaten, Namen, Webseitenzugriffe und sonstige\nDaten, die über eine Website generiert werden, handeln.")]),_c('p',[_vm._v("Der Einsatz des Hosters erfolgt zum Zwecke der Vertragserfüllung gegenüber unseren potenziellen und\nbestehenden Kunden (Art. 6 Abs. 1 lit. b DSGVO) und im Interesse einer sicheren, schnellen und effizienten\nBereitstellung unseres Online-Angebots durch einen professionellen Anbieter (Art. 6 Abs. 1 lit. f DSGVO).")]),_c('p',[_vm._v("Unser Hoster wird Ihre Daten nur insoweit verarbeiten, wie dies zur Erfüllung seiner Leistungspflichten\nerforderlich ist und unsere Weisungen in Bezug auf diese Daten befolgen.")]),_c('h2',[_vm._v("3. Allgemeine Hinweise und Pflichtinformationen")]),_c('h3',[_vm._v("Datenschutz")]),_c('p',[_vm._v("Die Betreiber dieser Seiten nehmen den Schutz Ihrer persönlichen Daten sehr ernst. Wir behandeln Ihre\npersonenbezogenen Daten vertraulich und entsprechend der gesetzlichen Datenschutzvorschriften sowie dieser\nDatenschutzerklärung.")]),_c('p',[_vm._v("Wenn Sie diese Website benutzen, werden verschiedene personenbezogene Daten erhoben. Personenbezogene Daten sind\nDaten, mit denen Sie persönlich identifiziert werden können. Die vorliegende Datenschutzerklärung\nerläutert, welche Daten wir erheben und wofür wir sie nutzen. Sie erläutert auch, wie und zu welchem\nZweck das geschieht.")]),_c('p',[_vm._v("Wir weisen darauf hin, dass die Datenübertragung im Internet (z.B. bei der Kommunikation per E-Mail)\nSicherheitslücken aufweisen kann. Ein lückenloser Schutz der Daten vor dem Zugriff durch Dritte ist nicht\nmöglich.")]),_c('h3',[_vm._v("Hinweis zur verantwortlichen Stelle")]),_c('p',[_vm._v(_vm._s(_vm.company))]),_c('p',[_vm._v("Verantwortliche Stelle ist die natürliche oder juristische Person, die allein oder gemeinsam mit anderen\nüber die Zwecke und Mittel der Verarbeitung von personenbezogenen Daten (z.B. Namen, E-Mail-Adressen o.\nÄ.) entscheidet.")]),_c('h3',[_vm._v("Widerruf Ihrer Einwilligung zur Datenverarbeitung")]),_c('p',[_vm._v("Viele Datenverarbeitungsvorgänge sind nur mit Ihrer ausdrücklichen Einwilligung möglich. Sie\nkönnen eine bereits erteilte Einwilligung jederzeit widerrufen. Dazu reicht eine formlose Mitteilung per E-Mail\nan uns. Die Rechtmäßigkeit der bis zum Widerruf erfolgten Datenverarbeitung bleibt vom Widerruf\nunberührt.")]),_c('h3',[_vm._v("Widerspruchsrecht gegen die Datenerhebung in besonderen Fällen sowie gegen Direktwerbung (Art. 21 DSGVO)")]),_c('p',[_vm._v("WENN DIE DATENVERARBEITUNG AUF GRUNDLAGE VON ART. 6 ABS. 1 LIT. E ODER F DSGVO ERFOLGT, HABEN SIE JEDERZEIT DAS\nRECHT, AUS GRÜNDEN, DIE SICH AUS IHRER BESONDEREN SITUATION ERGEBEN, GEGEN DIE VERARBEITUNG IHRER\nPERSONENBEZOGENEN DATEN WIDERSPRUCH EINZULEGEN; DIES GILT AUCH FÜR EIN AUF DIESE BESTIMMUNGEN GESTÜTZTES\nPROFILING. DIE JEWEILIGE RECHTSGRUNDLAGE, AUF DENEN EINE VERARBEITUNG BERUHT, ENTNEHMEN SIE DIESER\nDATENSCHUTZERKLÄRUNG. WENN SIE WIDERSPRUCH EINLEGEN, WERDEN WIR IHRE BETROFFENEN PERSONENBEZOGENEN DATEN NICHT\nMEHR VERARBEITEN, ES SEI DENN, WIR KÖNNEN ZWINGENDE SCHUTZWÜRDIGE GRÜNDE FÜR DIE VERARBEITUNG\nNACHWEISEN, DIE IHRE INTERESSEN, RECHTE UND FREIHEITEN ÜBERWIEGEN ODER DIE VERARBEITUNG DIENT DER\nGELTENDMACHUNG, AUSÜBUNG ODER VERTEIDIGUNG VON RECHTSANSPRÜCHEN (WIDERSPRUCH NACH ART. 21 ABS. 1 DSGVO).")]),_c('p',[_vm._v("WERDEN IHRE PERSONENBEZOGENEN DATEN VERARBEITET, UM DIREKTWERBUNG ZU BETREIBEN, SO HABEN SIE DAS RECHT, JEDERZEIT\nWIDERSPRUCH GEGEN DIE VERARBEITUNG SIE BETREFFENDER PERSONENBEZOGENER DATEN ZUM ZWECKE DERARTIGER WERBUNG\nEINZULEGEN; DIES GILT AUCH FÜR DAS PROFILING, SOWEIT ES MIT SOLCHER DIREKTWERBUNG IN VERBINDUNG STEHT. WENN SIE\nWIDERSPRECHEN, WERDEN IHRE PERSONENBEZOGENEN DATEN ANSCHLIESSEND NICHT MEHR ZUM ZWECKE DER DIREKTWERBUNG VERWENDET\n(WIDERSPRUCH NACH ART. 21 ABS. 2 DSGVO).")]),_c('h3',[_vm._v("Beschwerderecht bei der zuständigen Aufsichtsbehörde")]),_c('p',[_vm._v("Im Falle von Verstößen gegen die DSGVO steht den Betroffenen ein Beschwerderecht bei einer\nAufsichtsbehörde, insbesondere in dem Mitgliedstaat ihres gewöhnlichen Aufenthalts, ihres Arbeitsplatzes\noder des Orts des mutmaßlichen Verstoßes zu. Das Beschwerderecht besteht unbeschadet anderweitiger\nverwaltungsrechtlicher oder gerichtlicher Rechtsbehelfe.")]),_c('h3',[_vm._v("Recht auf Datenübertragbarkeit")]),_c('p',[_vm._v("Sie haben das Recht, Daten, die wir auf Grundlage Ihrer Einwilligung oder in Erfüllung eines Vertrags\nautomatisiert verarbeiten, an sich oder an einen Dritten in einem gängigen, maschinenlesbaren Format\naushändigen zu lassen. Sofern Sie die direkte Übertragung der Daten an einen anderen Verantwortlichen\nverlangen, erfolgt dies nur, soweit es technisch machbar ist.")]),_c('h3',[_vm._v("SSL- bzw. TLS-Verschlüsselung")]),_c('p',[_vm._v("Diese Seite nutzt aus Sicherheitsgründen und zum Schutz der Übertragung vertraulicher Inhalte, wie zum\nBeispiel Bestellungen oder Anfragen, die Sie an uns als Seitenbetreiber senden, eine SSL- bzw.\nTLS-Verschlüsselung. Eine verschlüsselte Verbindung erkennen Sie daran, dass die Adresszeile des Browsers\nvon „http://“ auf „https://“ wechselt und an dem Schloss-Symbol in Ihrer Browserzeile.")]),_c('p',[_vm._v("Wenn die SSL- bzw. TLS-Verschlüsselung aktiviert ist, können die Daten, die Sie an uns übermitteln,\nnicht von Dritten mitgelesen werden.")]),_c('h3',[_vm._v("Auskunft, Löschung und Berichtigung")]),_c('p',[_vm._v("Sie haben im Rahmen der geltenden gesetzlichen Bestimmungen jederzeit das Recht auf unentgeltliche Auskunft über\nIhre gespeicherten personenbezogenen Daten, deren Herkunft und Empfänger und den Zweck der Datenverarbeitung\nund ggf. ein Recht auf Berichtigung oder Löschung dieser Daten. Hierzu sowie zu weiteren Fragen zum Thema\npersonenbezogene Daten können Sie sich jederzeit unter der im Impressum angegebenen Adresse an uns wenden.")]),_c('h3',[_vm._v("Recht auf Einschränkung der Verarbeitung")]),_c('p',[_vm._v("Sie haben das Recht, die Einschränkung der Verarbeitung Ihrer personenbezogenen Daten zu verlangen. Hierzu\nkönnen Sie sich jederzeit unter der im Impressum angegebenen Adresse an uns wenden. Das Recht auf\nEinschränkung der Verarbeitung besteht in folgenden Fällen:")]),_c('ul',[_c('li',[_vm._v("Wenn Sie die Richtigkeit Ihrer bei uns gespeicherten personenbezogenen Daten bestreiten, benötigen wir in\nder Regel Zeit, um dies zu überprüfen. Für die Dauer der Prüfung haben Sie das Recht, die\nEinschränkung der Verarbeitung Ihrer personenbezogenen Daten zu verlangen.")]),_c('li',[_vm._v("Wenn die Verarbeitung Ihrer personenbezogenen Daten unrechtmäßig geschah/geschieht, können Sie\nstatt der Löschung die Einschränkung der Datenverarbeitung verlangen.")]),_c('li',[_vm._v("Wenn wir Ihre personenbezogenen Daten nicht mehr benötigen, Sie sie jedoch zur Ausübung, Verteidigung\noder Geltendmachung von Rechtsansprüchen benötigen, haben Sie das Recht, statt der Löschung die\nEinschränkung der Verarbeitung Ihrer personenbezogenen Daten zu verlangen.")]),_c('li',[_vm._v("Wenn Sie einen Widerspruch nach Art. 21 Abs. 1 DSGVO eingelegt haben, muss eine Abwägung zwischen Ihren und\nunseren Interessen vorgenommen werden. Solange noch nicht feststeht, wessen Interessen überwiegen, haben\nSie das Recht, die Einschränkung der Verarbeitung Ihrer personenbezogenen Daten zu verlangen.")])]),_c('p',[_vm._v("Wenn Sie die Verarbeitung Ihrer personenbezogenen Daten eingeschränkt haben, dürfen diese Daten – von\nihrer Speicherung abgesehen – nur mit Ihrer Einwilligung oder zur Geltendmachung, Ausübung oder\nVerteidigung von Rechtsansprüchen oder zum Schutz der Rechte einer anderen natürlichen oder juristischen\nPerson oder aus Gründen eines wichtigen öffentlichen Interesses der Europäischen Union oder eines\nMitgliedstaats verarbeitet werden.")]),_c('h3',[_vm._v("Widerspruch gegen Werbe-E-Mails")]),_c('p',[_vm._v("Der Nutzung von im Rahmen der Impressumspflicht veröffentlichten Kontaktdaten zur Übersendung von nicht\nausdrücklich angeforderter Werbung und Informationsmaterialien wird hiermit widersprochen. Die Betreiber der\nSeiten behalten sich ausdrücklich rechtliche Schritte im Falle der unverlangten Zusendung von\nWerbeinformationen, etwa durch Spam-E-Mails, vor.")]),_c('h2',[_vm._v("4. Datenerfassung auf dieser Website")]),_c('h3',[_vm._v("Cookies")]),_c('p',[_vm._v("Die Internetseiten verwenden teilweise so genannte Cookies. Cookies richten auf Ihrem Rechner keinen Schaden an und\nenthalten keine Viren. Cookies dienen dazu, unser Angebot nutzerfreundlicher, effektiver und sicherer zu machen.\nCookies sind kleine Textdateien, die auf Ihrem Rechner abgelegt werden und die Ihr Browser speichert.")]),_c('p',[_vm._v("Die meisten der von uns verwendeten Cookies sind so genannte „Session-Cookies“. Sie werden nach Ende\nIhres Besuchs automatisch gelöscht. Andere Cookies bleiben auf Ihrem Endgerät gespeichert bis Sie diese\nlöschen. Diese Cookies ermöglichen es uns, Ihren Browser beim nächsten Besuch w