 hat ein\nberechtigtes Interesse an der technisch fehlerfreien Darstellung und der Optimierung seiner Website – hierzu\nmüssen die Server-Log-Files erfasst werden.")]),_c('h3',[_vm._v("Kontaktformular")]),_c('p',[_vm._v("Wenn Sie uns per Kontaktformular Anfragen zukommen lassen, werden Ihre Angaben aus dem Anfrageformular inklusive der\nvon Ihnen dort angegebenen Kontaktdaten zwecks Bearbeitung der Anfrage und für den Fall von Anschlussfragen bei\nuns gespeichert. Diese Daten geben wir nicht ohne Ihre Einwilligung weiter.")]),_c('p',[_vm._v("Die Verarbeitung dieser Daten erfolgt auf Grundlage von Art. 6 Abs. 1 lit. b DSGVO, sofern Ihre Anfrage mit der\nErfüllung eines Vertrags zusammenhängt oder zur Durchführung vorvertraglicher Maßnahmen\nerforderlich ist. In allen übrigen Fällen beruht die Verarbeitung auf unserem berechtigten Interesse an\nder effektiven Bearbeitung der an uns gerichteten Anfragen (Art. 6 Abs. 1 lit. f DSGVO) oder auf Ihrer Einwilligung\n(Art. 6 Abs. 1 lit. a DSGVO) sofern diese abgefragt wurde.")]),_c('p',[_vm._v("Die von Ihnen im Kontaktformular eingegebenen Daten verbleiben bei uns, bis Sie uns zur Löschung auffordern,\nIhre Einwilligung zur Speicherung widerrufen oder der Zweck für die Datenspeicherung entfällt (z.B.\nnach abgeschlossener Bearbeitung Ihrer Anfrage). Zwingende gesetzliche Bestimmungen – insbesondere\nAufbewahrungsfristen – bleiben unberührt.")]),_c('h3',[_vm._v("Anfrage per E-Mail, Telefon oder Telefax")]),_c('p',[_vm._v("Wenn Sie uns per E-Mail, Telefon oder Telefax kontaktieren, wird Ihre Anfrage inklusive aller daraus hervorgehenden\npersonenbezogenen Daten (Name, Anfrage) zum Zwecke der Bearbeitung Ihres Anliegens bei uns gespeichert und\nverarbeitet. Diese Daten geben wir nicht ohne Ihre Einwilligung weiter.")]),_c('p',[_vm._v("Die Verarbeitung dieser Daten erfolgt auf Grundlage von Art. 6 Abs. 1 lit. b DSGVO, sofern Ihre Anfrage mit der\nErfüllung eines Vertrags zusammenhängt oder zur Durchführung vorvertraglicher Maßnah